.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.timeline{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 300px;
}
.settings{
  width: 200px;
  height: 55px;
  border-radius: 10px;
  color: white;
  font-weight: 500;
  font-size: 30px;
  background-color: black;
  text-decoration: none;
  align-self: center;
  margin-top: 40px;
}